import $ from 'jquery';
import '@/js/settings.js';
import renderer from '@/js/renderer';
import './summernote-sm.scss';
import { arrow } from '@popperjs/core';
//import { summernote_global_config } from './js/globalinit';

const editor = renderer.create('<div class="note-editor note-frame card"></div>');
const toolbar = renderer.create('<div class="note-toolbar note-toolbar-main card-header" role="toolbar"></div>');
const editingArea = renderer.create('<div class="note-editing-area"></div>');
const codable = renderer.create('<textarea class="note-codable" aria-multiline="true"></textarea>');
const editable = renderer.create('<div class="note-editable card-block" contentEditable="true" role="textbox" aria-multiline="true"></div>');
const statusbar = renderer.create([
  '<output class="note-status-output" role="status" aria-live="polite"></output>',
  '<div class="note-statusbar" role="status">',
    '<div class="note-statusbar-content">',
      '<div class="note-selection-path fwm"></div>',
      '<div class="note-zoomer d-flex hstack flex-nowrap gap-2">',
        '<button type="button" class="note-zoom-action btn btn-clear-dark btn-no-border btn-sm btn-icon rounded-circle" data-action="decr" title="Zoom out">&minus;</button>',
        '<span class="note-zoom-value text-nowrap" data-value="1">100 %</span>',
        '<button type="button" class="note-zoom-action btn btn-clear-dark btn-no-border btn-sm btn-icon rounded-circle" data-action="incr" title="Zoom in">&plus;</button>',
      '</div>',
    '</div>',
    '<div class="note-resizebar" aria-label="Resize">',
      '<div class="note-icon-bar"></div>',
      '<div class="note-icon-bar"></div>',
      '<div class="note-icon-bar"></div>',
    '</div>',
  '</div>',
].join(''));

const airEditor = renderer.create('<div class="note-editor note-airframe"></div>');
const airEditable = renderer.create([
  '<div class="note-editable" contentEditable="true" role="textbox" aria-multiline="true"></div>',
  '<output class="note-status-output" role="status" aria-live="polite"></output>',
].join(''));

const buttonGroup = renderer.create('<div class="note-btn-group btn-group"></div>');
//const toolGroup = renderer.create('<div class="note-btn-group btn-group"></div>');
const toolGroup = renderer.create('<div class="note-toolgroup"></div>');

const dropdown = renderer.create(
  '<div class="note-dropdown-menu dropdown-menu" role="list"></div>',
  function($node, options) {
    const markup = Array.isArray(options.items)
      ? options.items
        .map(function(item) {
          const value = typeof item === 'string' ? item : item.value || '';
          const content = options.template ? options.template(item) : item;
          const option = typeof item === 'object' ? item.option : undefined;

          const dataValue = 'data-value="' + value + '"';
          const dataOption = option !== undefined ? ' data-option="' + option + '"' : '';
          return (
            '<a class="dropdown-item" href="#" ' +
              (dataValue + dataOption) +
              ' role="listitem" aria-label="' +
              value +
              '">' +
              content +
              '</a>'
          );
        })
        .join('')
      : options.items;

    $node.html(markup).attr({ 'aria-label': options.title });

    if (options && options.codeviewKeepButton) {
      $node.addClass('note-codeview-keep');
    }
  },
);

const dropdownButtonContents = function(contents) {
  return contents;
};

const dropdownCheck = renderer.create(
  '<div class="note-dropdown-menu dropdown-menu note-check" role="list"></div>',
  function($node, options) {
    const markup = Array.isArray(options.items)
      ? options.items
        .map(function(item) {
          const value = typeof item === 'string' ? item : item.value || '';
          const content = options.template ? options.template(item) : item;
          return (
            '<a class="dropdown-item" href="#" data-value="' +
              value +
              '" role="listitem" aria-label="' +
              item +
              '">' +
              icon(options.checkClassName) +
              ' ' +
              content +
              '</a>'
          );
        })
        .join('')
      : options.items;
    $node.html(markup).attr({ 'aria-label': options.title });

    if (options && options.codeviewKeepButton) {
      $node.addClass('note-codeview-keep');
    }
  },
);

const dialog = renderer.create(
  '<div class="modal note-modal" aria-hidden="false" tabindex="-1" role="dialog"></div>',
  function($node, options) {
    if (options.fade) {
      $node.addClass('fade');
    }
    $node.attr({
      'aria-label': options.title,
    });
    $node.html(
      [
        '<div class="modal-dialog">',
        '<div class="modal-content">',
        options.title
          ? '<div class="modal-header">' +
            '<h4 class="modal-title">' +
            options.title +
            '</h4>' +
            '<button type="button" class="close" data-dismiss="modal" aria-label="Close" aria-hidden="true"></button>' +
            '</div>'
          : '',
        '<div class="modal-body">' + options.body + '</div>',
        options.footer ? '<div class="modal-footer">' + options.footer + '</div>' : '',
        '</div>',
        '</div>',
      ].join(''),
    );
  },
);

const popover = renderer.create([
  '<div class="note-popover popover bs-popover-auto show" x-arrow="arrow">',
    '<div class="arrow"></div>',
    '<div class="popover-content note-toolbar note-children-container"></div>',
  '</div>',
].join(''), function($node, options) {
  if (options.hideArrow) {
    $node.find('.arrow').hide();
  }
});

const checkbox = renderer.create('<div class="form-check"></div>', function($node, options) {
  $node.html([
    '<input type="checkbox" class="form-check-input"' + (options.id ? ' id="note-' + options.id + '"' : ''),
      (options.checked ? ' checked' : ''),
      ' aria-label="' + (options.text ? options.text : '') + '"',
      ' aria-checked="' + (options.checked ? 'true' : 'false') + '"/>',
    '<label class="form-check-label"' + (options.id ? ' for="note-' + options.id + '"' : '') + '>',
      ' ' + (options.text ? options.text : '') +
    '</label>',
  ].join(''));
});

const icon = function(iconClassName, tagName) {
  if (iconClassName.match(/^</)) {
    return iconClassName;
  }
  tagName = tagName || 'i';
  return '<' + tagName + ' class="' + iconClassName + '"></' + tagName+'>';
};

const ui = function(editorOptions) {
  return {
    editor: editor,
    toolbar: toolbar,
    editingArea: editingArea,
    codable: codable,
    editable: editable,
    statusbar: statusbar,
    airEditor: airEditor,
    airEditable: airEditable,
    buttonGroup: buttonGroup,
    toolGroup: toolGroup,
    dropdown: dropdown,
    dropdownButtonContents: dropdownButtonContents,
    dropdownCheck: dropdownCheck,
    dialog: dialog,
    popover: popover,
    icon: icon,
    checkbox: checkbox,
    options: editorOptions,

    palette: function($node, options) {
      return renderer.create('<div class="note-color-palette"></div>', function($node, options) {
        const contents = [];
        for (let row = 0, rowSize = options.colors.length; row < rowSize; row++) {
          const eventName = options.eventName;
          const colors = options.colors[row];
          const colorsName = options.colorsName[row];
          const buttons = [];
          for (let col = 0, colSize = colors.length; col < colSize; col++) {
            const color = colors[col];
            const colorName = colorsName[col];
            buttons.push([
              '<button type="button" class="note-color-btn"',
              'style="background-color:', color, '" ',
              'data-event="', eventName, '" ',
              'data-value="', color, '" ',
              'title="', colorName, '" ',
              'aria-label="', colorName, '" ',
              'data-toggle="button" tabindex="-1"></button>',
            ].join(''));
          }
          contents.push('<div class="note-color-row">' + buttons.join('') + '</div>');
        }
        $node.html(contents.join(''));

        if (options.tooltip) {
          var tooltipOptions = {
            container: options.container || editorOptions.container,
            trigger: 'hover',
            placement: 'bottom',
          };

          $node.tooltip({
            selector: '.note-color-btn',
            ...tooltipOptions,
          });
        }
      })($node, options);
    },

    button: function($node, options) {
      return renderer.create('<button type="button" class="note-btn btn btn-light btn-sm"></button>', function($node, options) {
        if (options && options.tooltip) {
          $node.attr({
            title: options.tooltip,
            'aria-label': options.tooltip,
          }).tooltip({
            container: options.container || editorOptions.container,
            trigger: 'hover',
            placement: 'bottom',
            popperConfig: { 
              modifiers: { 
                computeStyle: { gpuAcceleration: false },
                arrow: { element: '.arrow' },
              } 
            },
          }).on('click', (e) => {
            $(e.currentTarget).tooltip('hide');
          });
        }
        if (options && options.codeviewButton) {
          $node.addClass('note-codeview-keep');
        }
      })($node, options);
    },

    toggleBtn: function($btn, isEnable) {
      $btn.toggleClass('disabled', !isEnable);
      $btn.attr('disabled', !isEnable);
    },

    toggleBtnActive: function($btn, isActive) {
      $btn.toggleClass('active', isActive);
    },

    onDialogShown: function($dialog, handler) {
      $dialog.one('shown.bs.modal', handler);
    },

    onDialogHidden: function($dialog, handler) {
      $dialog.one('hidden.bs.modal', handler);
    },

    showDialog: function($dialog) {
      $dialog.modal('show');
    },

    hideDialog: function($dialog) {
      $dialog.modal('hide');
    },

    createLayout: function($note) {
      const $editor = (
        editorOptions.airMode
          ? airEditor([editingArea([codable(), airEditable()])])
          : editorOptions.toolbarPosition === 'bottom'
            ? editor([editingArea([codable(), editable()]), toolbar(), statusbar()])
            : editor([toolbar(), editingArea([codable(), editable()]), statusbar()])
      ).render();

      $editor.insertAfter($note);

      return {
        note: $note,
        editor: $editor,
        toolbar: $editor.find('.note-toolbar'),
        editingArea: $editor.find('.note-editing-area'),
        editable: $editor.find('.note-editable'),
        codable: $editor.find('.note-codable'),
        statusbar: $editor.find('.note-statusbar'),
      };
    },

    removeLayout: function($note, layoutInfo) {
      $note.html(layoutInfo.editable.html());
      layoutInfo.editor.remove();
      $note.show();
    },
  };
};

//.summernote.options = $.extend($.summernote.options, summernote_global_config);

$.summernote = $.extend($.summernote, {
  ui_template: ui,
  interface: 'sm',
});
